.popup-holder {
	display: inline;
	padding: 5px;
	position: relative;
	color: #000;
	div {

		&.popup {
			z-index: 999;
			position: fixed;
			background-color: #242424;
			top: 0px;
			bottom: 0;
			right: 0;
			left: 0px;
			display: none;
		}
	}

	a.open {
		display: block;
		height: 1%;
		color: #304e91;
		font-size: 18px;
	}

	&.popup-active {
		div {
			&.popup {
				display: flex !important;
				justify-content: center;
				align-items: center;
				padding: 10px;
			}
		}
	}
}