$nav-bc: #ccc;

// header style
.some-header-wrapper {
	position: relative;
}

.some-header {
	display: flex;
	justify-content: center;
	align-items: center;
}

.some-logo {
	margin-right: auto;
}

// menu
.nav-drop {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	z-index: 5;

	@include media("<=desktop") {
		overflow: hidden;
		padding: 0;
		position: absolute;
		left: 0;
		right: 0;
		top: 100%;
	}
}

.menu-main {
	&>li {
		@include liX(20px);

		&>a {
			padding: 10px 0;
			display: inline-block;
		}
	}
}

#nav {
	text-align: left;

	@include media("<=desktop") {
		width: 100%;
		padding: 0;
		transition: all .3s linear;
		transform: translateY(-100%);
		background-color: $nav-bc;
	}

	.nav-active & {
		transform: translateY(0);
	}

	.menu-main {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-content: center;
		@extend %listreset;

		@include media("<=desktop") {
			padding-left: $container-pading-left;
			padding-right: $container-pading-right;
		}

		@include media("<=tablet") {
			padding: 0 30px;
		}

		@include media("<=phone") {
			padding: 0 15px;
		}
	}

	li {
		position: relative;
		list-style: none;
		//fix bug edge browser
		list-style-image: url(data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7);

		@include media("<=desktop") {
			width: 100%;
			margin: 0;
		}

		a {
			text-decoration: none;

			@include media("<=desktop") {
				padding: 10px 40px 10px 0;
				display: block;
			}
		}

		.drop {
			position: absolute;
			z-index: 3;
			display: none;
			width: 180px;
			top: 100%;
			left: 50%;
			transform: translateX(-50%);
			background: $nav-bc;

			a {
				@include media(">desktop") {
					padding: 5px 10px;
					display: block;
				}
			}

			@include media("<=desktop") {
				position: static;
				width: 100%;
				transform: translateX(0);

				.drop {
					padding: 0px 5px 10px 5px;
				}

				.sub-menu>li {
					padding-left: 5px;
				}
			}

			.sub-menu {
				display: block;
				position: relative;
				border: 1px solid #000;
				padding: 0px;
				text-align: center;

				@include media("<=desktop") {
					text-align: left;
					background: $nav-bc;
					margin-left: -1px;
					margin-right: -1px;
				}
			}
		}

		&.hover {
			>.drop {
				display: block;
				cursor: pointer;

				@include media("<=desktop") {
					display: none;
				}

				.hover {
					.drop {
						left: 100%;
						transform: translateX(0);
						top: -1px;
					}
				}
			}
		}

		&:last-child {
			.drop {
				left: auto;
				right: 0;
				transform: translateX(0);
			}
		}

		&:nth-last-child(2),
		&:last-child {
			&.hover {
				.drop {
					.hover {
						.drop {
							left: auto;
							right: 100%;
						}
					}
				}
			}
		}
	}
}

.drop-opener {
	@include media("<=desktop") {
		width: 40px;
		height: 40px;
		position: absolute;
		right: 0px;
		top: 0px;
		transition: all 0.2s linear;
		z-index: 2;

		&:before {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 10px 0 10px 15px;
			border-color: transparent transparent transparent #333;
			transition: all 0.15s linear;

			.actives & {
				transform: translate(-50%, -50%) rotate(90deg);
			}
		}
	}
}

.nav-opener {
	display: none;
	position: absolute;
	right: 50px;
	top: 50%;
	transform: translateY(-50%);
	width: 40px;
	height: 40px;
	text-indent: -9999px;
	overflow: hidden;

	@include media("<=desktop") {
		display: block;
	}

	@include media("<=tablet") {
		right: 30px;
	}

	@include media("<=phone") {
		right: 15px;
	}

	&:hover {
		opacity: .8;
	}

	&:before,
	&:after,
	span {
		background: $black;
		border-radius: 4px;
		position: absolute;
		top: 50%;
		left: 12%;
		right: 12%;
		height: 4px;
		margin-top: -2px;
		transition: all 0.2s linear;
	}

	span {
		.nav-active & {
			opacity: 0;
		}
	}

	&:before {
		content: "";
		top: 30%;

		.nav-active & {
			transform: rotate(45deg);
			top: 50%;
			left: 12%;
			right: 12%;
		}
	}

	&:after {
		content: "";
		top: 70%;

		.nav-active & {
			transform: rotate(-45deg);
			top: 50%;
			left: 12%;
			right: 12%;
		}
	}
}