/* Compatibility styles for frameworks like bootstrap, foundation e.t.c */
.xzoom-source img,
.xzoom-preview img,
.xzoom-lens img {
	display: block;
	max-width: none;
	max-height: none;
	transition: none;
}

/* --------------- */
/* xZoom Styles below */
.xzoom-container {
	display: inline-block;
}

.xzoom-thumbs {
	text-align: center;
	margin-bottom: 10px;
}

.xzoom {
	box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);
}

.xzoom2,
.xzoom3,
.xzoom4,
.xzoom5 {
	box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);
}

/* Thumbs */
.xzoom-gallery,
.xzoom-gallery2,
.xzoom-gallery3,
.xzoom-gallery4,
.xzoom-gallery5 {
	border: 1px solid #cecece;
	margin-left: 5px;
	margin-bottom: 10px;
}

.xzoom-source,
.xzoom-hidden {
	display: block;
	position: static;
	float: none;
	clear: both;
}

/* Everything out of border is hidden */
.xzoom-hidden {
	overflow: hidden;
}

/* Preview */
.xzoom-preview {
	border: 1px solid #888;
	background: #2f4f4f;
	box-shadow: -0px -0px 10px rgba(0, 0, 0, 0.50);
}

/* Lens */
.xzoom-lens {
	border: 1px solid #555;
	box-shadow: -0px -0px 10px rgba(0, 0, 0, 0.50);
	cursor: crosshair;
}

/* Loading */
.xzoom-loading {
	background-position: center center;
	background-repeat: no-repeat;
	border-radius: 100%;
	opacity: .7;
	// background: url(../images/xloading.gif);
	width: 48px;
	height: 48px;
}

/* Additional class that applied to thumb when it is active */
.xactive {
	box-shadow: 0px 0px 3px 0px rgba(74, 169, 210, 1);
	border: 1px solid #4aaad2;
}

/* Caption */
.xzoom-caption {
	position: absolute;
	bottom: -43px;
	left: 0;
	background: #000;
	width: 100%;
	text-align: left;
}

.xzoom-caption span {
	color: #fff;
	font-family: Arial, sans-serif;
	display: block;
	font-size: 0.75em;
	font-weight: bold;
	padding: 10px;
}