// variable
$focus-color-jcf: blue;

// #radio
.jcf-radio {
	vertical-align: middle;
	display: inline-block;
	position: relative;
	overflow: hidden;
	cursor: default;
	background: #fff;
	border: 1px solid #777;
	border-radius: 9px;
	margin: 0 3px 0 0;
	height: 16px;
	width: 16px;
	transition: border-color .2s ease;

	span {
		opacity: 0;
		visibility: hidden;
		position: absolute;
		top: 3px;
		left: 3px;
		right: 3px;
		bottom: 3px;
		background: #777;
		border-radius: 100%;
		transition: border-color .2s ease, opacity .2s ease, visibility .2s ease;
	}

	input[type="radio"] {
		position: absolute;
		height: 100%;
		width: 100%;
		border: 0;
		margin: 0;
		left: 0;
		top: 0;
	}

	&.jcf-checked span {
		visibility: visible;
		opacity: 1;
	}
}

// #checkbox
.jcf-checkbox {
	vertical-align: middle;
	display: inline-block;
	position: relative;
	overflow: hidden;
	cursor: default;
	background: #fff;
	border: 1px solid #777;
	margin: 0 3px 0 0;
	height: 16px;
	width: 16px;
	transition: border-color .2s ease;

	span {
		position: absolute;
		opacity: 0;
		visibility: hidden;
		height: 7px;
		width: 10px;
		top: 50%;
		left: 50%;
		margin: -7px 0 0 -6px;
		border: 3px solid #777;
		border-width: 0 0 3px 3px;
		transform: rotate(-45deg);
		transition: border-color .2s ease, opacity .2s ease, visibility .2s ease;

		html & {
			margin: -4px 0 0 -5px;
		}
	}

	input[type="checkbox"] {
		position: absolute;
		width: 100%;
		height: 100%;
		border: 0;
		margin: 0;
		left: 0;
		top: 0;
	}

	&.jcf-checked span {
		visibility: visible;
		opacity: 1;
	}
}

// #file
.jcf-file {
	display: inline-block;
	white-space: nowrap;
	position: relative;
	overflow: hidden;
	background: #fff;

	.jcf-real-element {
		position: absolute;
		font-size: 200px;
		height: 200px;
		margin: 0;
		right: 0;
		top: 0;
	}

	.jcf-fake-input {
		display: inline-block;
		text-overflow: ellipsis;
		white-space: nowrap;
		vertical-align: top;
		overflow: hidden;
		border: 1px solid #777;
		padding: 0 8px;
		font-size: 13px;
		line-height: 28px;
		height: 28px;
		width: 125px;
	}

	.jcf-upload-button {
		display: inline-block;
		vertical-align: top;
		white-space: nowrap;
		overflow: hidden;
		border: 1px solid #777;
		margin: 0 0 0 -1px;
		padding: 0 10px;
		line-height: 28px;
		height: 28px;
	}
}

// #select
.jcf-select {
	display: inline-block;
	vertical-align: top;
	position: relative;
	border: 1px solid #777;
	background: #fff;
	margin: 0 0 12px;
	max-width: 200px;
	width: 100%;
	height: 26px;

	select {
		z-index: 1;
		left: 0;
		top: 0;
	}

	.jcf-select-text {
		text-overflow: ellipsis;
		white-space: nowrap;
		overflow: hidden;
		cursor: default;
		display: block;
		font-size: 13px;
		line-height: 26px;
		margin: 0 35px 0 8px;
	}

	.jcf-select-opener {
		position: absolute;
		text-align: center;
		background: #aaa;
		width: 26px;
		bottom: 0;
		right: 0;
		top: 0;
	}

	.jcf-select-drop {
		position: absolute;
		margin-top: 0px;
		z-index: 9999;
		top: 100%;
		left: -1px;
		right: -1px;

		.jcf-select-drop-content {
			border: 1px solid #f00;
		}

		&.jcf-compact-multiple {

			// max-width: 220px;
			.jcf-hover {
				background: none;
			}

			.jcf-selected {
				background: #e6e6e6;
				color: #000;

				&:before {
					display: inline-block;
					content: '';
					height: 4px;
					width: 8px;
					margin: -7px 5px 0 -3px;
					border: 3px solid #777;
					border-width: 0 0 3px 3px;
					transform: rotate(-45deg);
					-ms-filter: "progid:DXImageTransform.Microsoft.Matrix(M11=0.7071067811865483, M12=0.7071067811865467, M21=-0.7071067811865467, M22=0.7071067811865483, SizingMethod='auto expand')";
				}
			}

			.jcf-select-opener {
				&:before {
					display: inline-block;
					padding-top: 2px;
					content: '...';
				}
			}
		}
	}

	.jcf-drop-flipped {
		bottom: 100%;
		top: auto;
	}
}

.jcf-list-box {
	overflow: hidden;
	display: inline-block;
	border: 1px solid #b8c3c9;
	min-width: 200px;
	margin: 0 15px;
}

.jcf-list {
	display: inline-block;
	vertical-align: top;
	position: relative;
	background: #fff;
	line-height: 14px;
	font-size: 12px;
	width: 100%;

	.jcf-list-content {
		vertical-align: top;
		display: inline-block;
		overflow: auto;
		width: 100%;
	}

	ul {
		list-style: none;
		padding: 0;
		margin: 0;

		li {
			overflow: hidden;
			display: block;
		}
	}

	.jcf-overflow {
		overflow: auto;
	}

	.jcf-option {
		white-space: nowrap;
		overflow: hidden;
		cursor: default;
		display: block;
		padding: 5px 9px;
		color: #656565;
		min-height: 14px;
		height: 1%;
	}

	.jcf-disabled {
		background: #fff !important;
		color: #aaa !important;
	}

	.jcf-optgroup-caption {
		white-space: nowrap;
		font-weight: bold;
		display: block;
		padding: 5px 9px;
		cursor: default;
		color: #000;
	}

	.jcf-optgroup .jcf-option {
		padding-left: 30px;
	}
}

.jcf-select-drop .jcf-hover,
.jcf-list-box .jcf-selected {
	background: #e6e6e6;
	color: #000;
}

body>.jcf-select-drop {
	position: absolute;
	margin: -1px 0 0;
	z-index: 9999;
}

body>.jcf-select-drop.jcf-drop-flipped {
	margin: 1px 0 0;
}

// #number
.jcf-number {
	display: inline-block;
	position: relative;
	height: 32px;

	input {
		-moz-appearance: textfield;
		box-sizing: border-box;
		border: 1px solid #777;
		padding: 3px 27px 3px 7px;
		margin: 0;
		height: 100%;
	}

	input::-webkit-inner-spin-button,
	input::-webkit-outer-spin-button {
		-webkit-appearance: none;
	}

	.jcf-btn-dec,
	.jcf-btn-inc {
		position: absolute;
		background: #aaa;
		width: 20px;
		height: 15px;
		right: 1px;
		top: 1px;
	}

	.jcf-btn-dec {
		top: auto;
		bottom: 1px;
	}

	.jcf-btn-dec:hover,
	.jcf-btn-inc:hover {
		background: #e6e6e6;
	}

	&.jcf-disabled .jcf-btn-dec:hover,
	&.jcf-disabled .jcf-btn-inc:hover {
		background: #aaa;
	}

	.jcf-btn-dec:before,
	.jcf-btn-inc:before {
		position: absolute;
		content: '';
		width: 0;
		height: 0;
		top: 50%;
		left: 50%;
		margin: -6px 0 0 -4px;
		border: 4px solid #aaa;
		border-color: transparent transparent #000 transparent;
	}

	.jcf-btn-dec:before {
		margin: -1px 0 0 -4px;
		border-color: #000 transparent transparent transparent;
	}

	&.jcf-disabled .jcf-btn-dec:before,
	&.jcf-disabled .jcf-btn-inc:before,
	& .jcf-btn-dec.jcf-disabled:before,
	& .jcf-btn-inc.jcf-disabled:before {
		opacity: 0.3;
	}

	&.jcf-disabled input {
		background: #ddd;
	}

	.jcf-disabled {
		background: #ddd !important;
	}
}

// #focus
.jcf-focus,
.jcf-focus * {
	border-color: $focus-color-jcf !important;
}

// #option color and other
.jcf-select-drop .jcf-option-hideme {
	display: none;
}

// for image on option
.jcf-select.jcf-select-present {
	line-height: 48px;
	height: 48px;

	.jcf-select-text img {
		vertical-align: top;
		width: 32px;
		height: auto;
		margin: 7px 5px 0 0;
	}

	.jcf-select-text {
		line-height: 48px;
	}

	.jcf-list {
		font-style: italic;
		line-height: 48px;
	}

	.jcf-list img {
		vertical-align: middle;
		margin: 0 5px 0 0;
	}
}

// my custom option
.jcf-select {
	.jcf-option-circle {
		position: relative;
		padding-left: 25px;

		&:before {
			content: "";
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0px;
			@include size(16px);
			border-radius: 50%;
			background-color: #000;
		}
	}

	.jcf-select-drop-content {
		.jcf-option-circle {
			&:before {
				left: 5px;
			}
		}
	}

	.jcf-option-green {
		&:before {
			background-color: green;
		}
	}

	.jcf-option-red {
		&:before {
			background-color: red;
		}
	}
}