.very-specific-design {
	width: 600px;
	height: 400px;
	padding: 50px;
	text-align: center;
	background: white;
	position: relative;
	left: 50%;
	top: 50%;
	-webkit-transform: translate3d(-50%, 0, 0);
	transform: translate3d(-50%, 0, 0);
	-webkit-transform-origin: center top;
	transform-origin: center top;
}