#wrapper-par {
	overflow: hidden;
	position: relative;
}

.section-par {
	overflow: hidden;
	position: relative;

	.bg-holder {
		background-color: #f00;
		min-height: 100vh;
		overflow: hidden;
		position: relative;
		background-attachment: fixed;
		background-repeat: no-repeat;

		&.autoheight {
			height: auto;

			.bg-block {
				background: rgba(255, 255, 255, .5);
				color: #000;
				margin: 0 auto;
				max-width: 984px;
				padding: 20px;
				position: relative;
				z-index: 1;
			}
		}
	}

	img {
		vertical-align: top;
	}

	.bg-frame {
		img {
			height: auto;
			position: absolute;
			width: auto;
		}
	}
}

.fallback-class {
	background-size: cover;
}