.inner {
	.anchor-nav {
		position: absolute;
	}
}

.anchor-nav {
	padding: 0;
	margin: 0;
	list-style: none;
	display: table;
	table-layout: fixed;
	width: 100%;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	background: #fff;

	li {
		border: 1px solid #bbb;
		display: table-cell;
		vertical-align: middle;
		text-align: center;
	}

	a {
		display: block;
		text-transform: uppercase;
		font-weight: bold;
		padding: 20px 12px;
		text-decoration: none;
		color: #0080ff;
	}

	.anchor-active {
		background: #eee;
	}
}

.anchor-nav-bottom {
	position: fixed;
	bottom: 0;
	width: 100%;
	left: 0;
}

.extra-anchor-nav {
	position: fixed;
	top: 100px;
	left: 0;
}

.section-active {
	background: #ccc;
}

.side-nav {
	position: fixed;
	background: #fff;
	left: 0;
	top: 50%;
	padding: 0;
	margin: 0;
}

.side-nav .anchor-active,
.inner-links .anchor-active a {
	color: #f00;
}

.inner-links {
	margin: 50px 0 0;
}

.inner-container {
	border: 1px solid #f00;
	background: #fff;
	overflow: auto;
	width: 600px;
	height: 300px;
	margin: 0 0 30px;
}