.grid-css-col {
	background-color: tomato;
	padding: 20px;
}


.grid-css-wrapper1 {
	display: grid;
	grid-row-gap: 20px;
	grid-column-gap: 10px;
	// упрощенная запись (есть возможности задать сразу два параметра)
	// grid-gap: 20px 10px;
	grid-template-rows: repeat(4, 50px);
	grid-template-columns: 200px 1fr 25% auto;
	// упрощенная запись (есть возможность задать сразу два параметра)
	// grid-template:  repeat(4, 50px)/200px 1fr 25% auto;
	margin-bottom: 30px;
	grid-template-areas:
		". col9 col8 col7"
		"col2 . col8 col6"
		"col2 col4 . col6"
		"col3 col4 col1 col5";
	margin-bottom: 30px;

	@include media("<=desktop") {
		grid-template-columns: repeat(4, 1fr)
	}

	.grid-css-col {}

	.grid-css-col1 {
		grid-area: col1;
	}

	.grid-css-col2 {
		grid-area: col2;
	}

	.grid-css-col3 {
		grid-area: col3;
	}

	.grid-css-col4 {
		grid-area: col4;
	}

	.grid-css-col5 {
		grid-area: col5;
	}

	.grid-css-col6 {
		grid-area: col6;
	}

	.grid-css-col7 {
		grid-area: col7;
	}

	.grid-css-col8 {
		grid-area: col8;
	}

	.grid-css-col9 {
		grid-area: col9;
	}
}