#fullpage {
	margin: 0 auto;
	// max-width: 1024px;
}

#fp-header,
#fp-footer {
	text-align: center;
	background: #ffa;
	font-size: 70px;
	padding: 30px;
	flex: 0 1 auto;
}

.fp-section-holder {
	display: flex;
	flex-wrap: wrap;
	align-content: stretch;
	align-items: stretch;
	flex-direction: column;
	height: 100%;

	.fp-section-content {
		display: flex;
		justify-content: center;
		align-content: center;
		align-items: center;
		flex: 1 1 auto;
	}
}

.fp-section {
	position: relative;
	text-align: center;
	font-size: 3em;
}

