ul {
	list-style: none;
	padding: 0;
	margin: 0;

	&.dropdown-nav {
		font: 13px;
		width: 100%;

		li {
			border: 1px solid #777;
			position: relative;
			margin: 0 -1px 0 0;
			float: left;
		}

		ul {
			list-style: none;
			padding: 0;
			margin: 0;
			border: 1px solid #777;
			border-width: 0 1px 1px;
			position: absolute;
			background: #eee;
			display: none;
			width: 200px;
			z-index: 1;
			top: 25px;
			left: 0;
			z-index: 1;

			li {
				border-width: 1px 0 0;
				margin: 0;
				width: 100%;
				float: left;
			}

			a {
				display: block;
				height: 1%;
				float: none;
			}

			ul {
				display: none;
				left: 200px;
				top: 0;
			}
		}

		a {
			text-decoration: none;
			background: #f5f5f5;
			padding: 5px 10px;
			float: left;
			color: #000;

			&:hover {
				text-decoration: none;
			}
		}
	}

	&.fade-nav {
		.drop-slide-wrapper {
			.drop-slide-wrapper {
				margin-left: 0;
			}
		}
	}
}

ul.fade-nav ul,
ul.dropdown-nav .drop-slide-wrapper {
	margin-left: -1px;
}

ul.dropdown-nav:after,
ul.dropdown-nav ul:after {
	clear: both;
	content: "";
	display: block;
}

ul.dropdown-nav li:hover ul,
ul.dropdown-nav li.hover ul {
	display: block;
}

ul.dropdown-nav li:hover ul ul,
ul.dropdown-nav li.hover ul ul {
	display: none;
}

ul.dropdown-nav li:hover ul li:hover ul,
ul.dropdown-nav li.hover ul li.hover ul {
	display: block;
}

ul.dropdown-nav li:hover a,
ul.dropdown-nav li.hover a,
ul.dropdown-nav li:hover ul li:hover a,
ul.dropdown-nav li.hover ul li.hover a,
ul.dropdown-nav li:hover ul li:hover ul li:hover a,
ul.dropdown-nav li.hover ul li.hover ul li.hover a {
	text-decoration: none;
	background: #777;
	color: #fff;
}

ul.dropdown-nav li:hover ul a,
ul.dropdown-nav li.hover ul a,
ul.dropdown-nav li:hover ul li:hover ul a,
ul.dropdown-nav li.hover ul li.hover ul a {
	background: #f5f5f5;
	color: #000;
}