.input-item {
	position: relative;
	display: inline-block;
}

.remove {
	position: absolute;
	top: 10px;
	left: auto;
	margin-left: -20px;
	color: #bfbebe;
}

.btn-add {
	text-decoration: none;
	color: #00A08A;
}

.row-group {
	counter-reset: item;

	& >.member-item {
		display: flex;
		flex-direction: column;
		counter-increment: memberitem;
		flex-wrap: nowrap;
		width: 100%;
		margin-bottom: 15px;

		.member-num {
			&:after {
				content: counters(memberitem, '.') '';
			}
		}
	}
}