.irs {
	position: relative;
	display: block;
	user-select: none;
	font-size: 12px;
	font-family: Arial, sans-serif;
}

.irs-line {
	position: relative;
	display: block;
	overflow: hidden;
	outline: none !important;
}

.irs-bar {
	position: absolute;
	display: block;
	left: 0;
	width: 0;
}

.irs-shadow {
	position: absolute;
	display: none;
	left: 0;
	width: 0;
}

.irs-handle {
	position: absolute;
	display: block;
	box-sizing: border-box;
	cursor: default;
	z-index: 1;

	&.type_last {
		z-index: 2;
	}
}

.irs-min,
.irs-max {
	position: absolute;
	display: block;
	cursor: default;
}

.irs-min {
	left: 0;
}

.irs-max {
	right: 0;
}

.irs-from,
.irs-to,
.irs-single {
	position: absolute;
	display: block;
	top: 0;
	left: 0;
	cursor: default;
	white-space: nowrap;
}

.irs-grid {
	position: absolute;
	display: none;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 20px;
}

.irs-with-grid {
	.irs-grid {
		display: block;
	}
}

.irs-grid-pol {
	position: absolute;
	top: 0;
	left: 0;
	width: 1px;
	height: 8px;
	background: #000;

	&.small {
		height: 4px;
	}
}

.irs-grid-text {
	position: absolute;
	bottom: 0;
	left: 0;
	white-space: nowrap;
	text-align: center;
	font-size: 9px;
	line-height: 9px;
	padding: 0 3px;
	color: #000;
}

.irs-disable-mask {
	position: absolute;
	display: block;
	top: 0;
	left: -1%;
	width: 102%;
	height: 100%;
	cursor: default;
	background: rgba(0, 0, 0, 0);
	z-index: 2;
}

.lt-ie9 {
	.irs-disable-mask {
		background: #000;
		filter: alpha(opacity=0);
		cursor: not-allowed;
	}
}

.irs-disabled {
	opacity: 0.4;
}

.irs-hidden-input {
	position: absolute !important;
	display: block !important;
	top: 0 !important;
	left: 0 !important;
	width: 0 !important;
	height: 0 !important;
	font-size: 0 !important;
	line-height: 0 !important;
	padding: 0 !important;
	margin: 0 !important;
	overflow: hidden;
	outline: none !important;
	z-index: -9999 !important;
	background: none !important;
	border-style: solid !important;
	border-color: transparent !important;
}

.irs--flat {
	height: 40px;

	&.irs-with-grid {
		height: 60px;
	}

	.irs-line {
		top: 25px;
		height: 12px;
		background-color: #e1e4e9;
		border-radius: 4px;
	}

	.irs-bar {
		top: 25px;
		height: 12px;
		background-color: #ed5565;
	}

	.irs-bar--single {
		border-radius: 4px 0 0 4px;
	}

	.irs-shadow {
		height: 1px;
		bottom: 16px;
		background-color: #e1e4e9;
	}

	.irs-handle {
		top: 22px;
		width: 16px;
		height: 18px;
		background-color: transparent;

		&>i {
			&:first-child {
				position: absolute;
				display: block;
				top: 0;
				left: 50%;
				width: 2px;
				height: 100%;
				margin-left: -1px;
				background-color: #da4453;
			}
		}
	}

	.irs-grid-pol {
		background-color: #e1e4e9;
	}

	.irs-grid-text {
		color: #999;
	}

	.irs-handle.state_hover>i:first-child,
	.irs-handle:hover>i:first-child {
		background-color: #a43540;
	}

	.irs-min,
	.irs-max {
		top: 0;
		padding: 1px 3px;
		color: #999;
		font-size: 10px;
		line-height: 1.333;
		text-shadow: none;
		background-color: #e1e4e9;
		border-radius: 4px;
	}

	.irs-from,
	.irs-to,
	.irs-single {
		color: white;
		font-size: 10px;
		line-height: 1.333;
		text-shadow: none;
		padding: 1px 5px;
		background-color: #ed5565;
		border-radius: 4px;
	}

	.irs-from:before,
	.irs-to:before,
	.irs-single:before {
		position: absolute;
		display: block;
		content: "";
		bottom: -6px;
		left: 50%;
		width: 0;
		height: 0;
		margin-left: -3px;
		overflow: hidden;
		border: 3px solid transparent;
		border-top-color: #ed5565;
	}
}

.irs--big {
	height: 55px;

	&.irs-with-grid {
		height: 70px;
	}

	.irs-line {
		top: 33px;
		height: 12px;
		background-color: white;
		background: linear-gradient(to bottom, #ddd -50%, white 150%);
		border: 1px solid #ccc;
		border-radius: 12px;
	}

	.irs-bar {
		top: 33px;
		height: 12px;
		background-color: #92bce0;
		border: 1px solid #428bca;
		background: linear-gradient(to bottom, #ffffff 0%, #428bca 30%, #b9d4ec 100%);
		box-shadow: inset 0 0 1px 1px rgba(255, 255, 255, 0.5);
	}

	.irs-bar--single {
		border-radius: 12px 0 0 12px;
	}

	.irs-shadow {
		height: 1px;
		bottom: 16px;
		background-color: rgba(66, 139, 202, 0.5);
	}

	.irs-handle {
		top: 25px;
		width: 30px;
		height: 30px;
		border: 1px solid rgba(0, 0, 0, 0.3);
		background-color: #cbcfd5;
		background: linear-gradient(to bottom, white 0%, #B4B9BE 30%, white 100%);
		box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2), inset 0 0 3px 1px white;
		border-radius: 30px;
	}

	.irs-grid-pol {
		background-color: #428bca;
	}

	.irs-grid-text {
		color: #428bca;
	}

	.irs-handle.state_hover,
	.irs-handle:hover {
		border-color: rgba(0, 0, 0, 0.45);
		background-color: #939ba7;
		background: linear-gradient(to bottom, white 0%, #919BA5 30%, white 100%);
	}

	.irs-min,
	.irs-max {
		top: 0;
		padding: 1px 5px;
		color: white;
		text-shadow: none;
		background-color: #9f9f9f;
		border-radius: 3px;
	}

	.irs-from,
	.irs-to,
	.irs-single {
		color: white;
		text-shadow: none;
		padding: 1px 5px;
		background-color: #428bca;
		background: linear-gradient(to bottom, #428bca 0%, #3071a9 100%);
		border-radius: 3px;
	}
}

.irs--modern {
	height: 55px;

	&.irs-with-grid {
		height: 55px;
	}

	.irs-line {
		top: 25px;
		height: 5px;
		background-color: #d1d6e0;
		background: linear-gradient(to bottom, #e0e4ea 0%, #d1d6e0 100%);
		border: 1px solid #a3adc1;
		border-bottom-width: 0;
		border-radius: 5px;
	}

	.irs-bar {
		top: 25px;
		height: 5px;
		background: #20b426;
		background: linear-gradient(to bottom, #20b426 0%, #18891d 100%);
	}

	.irs-bar--single {
		border-radius: 5px 0 0 5px;
	}

	.irs-shadow {
		height: 1px;
		bottom: 21px;
		background-color: rgba(209, 214, 224, 0.5);
	}

	.irs-handle {
		top: 37px;
		width: 12px;
		height: 13px;
		border: 1px solid #a3adc1;
		border-top-width: 0;
		box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
		border-radius: 0 0 3px 3px;

		&>i {
			&:nth-child(1) {
				position: absolute;
				display: block;
				top: -4px;
				left: 1px;
				width: 6px;
				height: 6px;
				border: 1px solid #a3adc1;
				background: white;
				transform: rotate(45deg);
			}

			&:nth-child(2) {
				position: absolute;
				display: block;
				box-sizing: border-box;
				top: 0;
				left: 0;
				width: 10px;
				height: 12px;
				background: #e9e6e6;
				background: linear-gradient(to bottom, white 0%, #e9e6e6 100%);
				border-radius: 0 0 3px 3px;
			}

			&:nth-child(3) {
				position: absolute;
				display: block;
				box-sizing: border-box;
				top: 3px;
				left: 3px;
				width: 4px;
				height: 5px;
				border-left: 1px solid #a3adc1;
				border-right: 1px solid #a3adc1;
			}
		}
	}

	.irs-grid {
		height: 25px;
	}

	.irs-grid-pol {
		background-color: #dedede;
	}

	.irs-grid-text {
		color: silver;
		font-size: 13px;
	}

	.irs-handle.state_hover,
	.irs-handle:hover {
		border-color: #7685a2;
		background: #c3c7cd;
		background: linear-gradient(to bottom, #ffffff 0%, #919ba5 30%, #ffffff 100%);
	}

	.irs-handle.state_hover>i:nth-child(1),
	.irs-handle:hover>i:nth-child(1) {
		border-color: #7685a2;
	}

	.irs-handle.state_hover>i:nth-child(3),
	.irs-handle:hover>i:nth-child(3) {
		border-color: #48536a;
	}

	.irs-min,
	.irs-max {
		top: 0;
		font-size: 10px;
		line-height: 1.333;
		text-shadow: none;
		padding: 1px 5px;
		color: white;
		background-color: #d1d6e0;
		border-radius: 5px;
	}

	.irs-from,
	.irs-to,
	.irs-single {
		font-size: 10px;
		line-height: 1.333;
		text-shadow: none;
		padding: 1px 5px;
		background-color: #20b426;
		color: white;
		border-radius: 5px;

		&:before {
			position: absolute;
			display: block;
			content: "";
			bottom: -6px;
			left: 50%;
			width: 0;
			height: 0;
			margin-left: -3px;
			overflow: hidden;
			border: 3px solid transparent;
			border-top-color: #20b426;
		}
	}
}

.irs--sharp {
	height: 50px;
	font-size: 12px;
	line-height: 1;

	&.irs-with-grid {
		height: 57px;
	}

	.irs-line {
		top: 30px;
		height: 2px;
