	}

	.irs-shadow {
		height: 1px;
		bottom: 21px;
		background-color: rgba(0, 0, 0, 0.5);
	}

	.irs-handle {
		top: 25px;
		width: 10px;
		height: 10px;
		background-color: #a804b2;

