.irs--square {
	height: 50px;

	&.irs-with-grid {
		height: 60px;
	}

	.irs-line {
		top: 31px;
		height: 4px;
		background-color: #dedede;
	}

	.irs-bar {
		top: 31px;
		height: 4px;
		background-color: black;
	}

	.irs-shadow {
		height: 2px;
		bottom: 21px;
		background-color: #dedede;
	}

	.irs-handle {
		top: 25px;
		width: 16px;
		height: 16px;
		border: 3px solid black;
		background-color: white;
		transform: rotate(45deg);
	}

	.irs-grid {
		height: 25px;
	}

	.irs-grid-pol {
		background-color: #dedede;
	}

	.irs-grid-text {
		color: silver;
		font-size: 11px;
	}

	.irs-handle.state_hover,
	.irs-handle:hover {
		background-color: #f0f6ff;
	}

	.irs-min,
	.irs-max {
		color: #333;
		font-size: 14px;
		line-height: 1;
		top: 0;
		padding: 3px 5px;
		background-color: rgba(0, 0, 0, 0.1);
	}

	.irs-from,
	.irs-to,
	.irs-single {
		font-size: 14px;
		line-height: 1;
		text-shadow: none;
		padding: 3px 5px;
		background-color: black;
		color: white;
