.flex-center-example {
	.blog {
		padding: 5px 15px;
		font-size: 20px;
		max-width: 600px;
		margin: 0 auto;
	}

	.blogItem {
		padding: 5px;
		margin: 20px 0;
		border: 1px solid black;
		border-radius: 10px;
		display: flex;
		flex-wrap: wrap;
	}

	.blogItem__cat {
		flex-grow: 0.5;
		margin-left: auto;
		padding: 5px;
	}

	.blogItem__dt {
		flex-grow: 0.5;
		text-align: right;
		padding: 5px;
	}

	.ib-center {
		display: inline-block;
		text-align: center;
	}

	.container-ex  {
		max-width: 1180px;
		margin: 0 auto;
		padding: 0 30px;
	}

	.header-ex  {
		background: #f56262;
		color: #fff;
	}

	.row-ex {
		display: flex;
		flex-wrap: wrap;
		align-items: baseline;
		padding: 10px 0;
	}

	.logo-ex  {
		flex-grow: 0.5;
		margin: 10px 15px 10px auto;
	}

	.menu-ex  {
		flex-grow: 0.5;
		text-align: right;
		margin: 10px 0;
	}

	.logo__title {
		display: inline-block;
		font-size: 1.8rem;
		line-height: 1.5rem;
		vertical-align: bottom;
	}

	.logo__image {
		vertical-align: bottom;
	}

	.menuItem {
		margin: 0 15px;
	}

	.titles {
		text-align: center;
		padding: 20px 0;
	}
}