.box-block {
	overflow: hidden;
	padding-bottom: 1px;
}
.box{
	width:100px;
	height:100px;
	background:#bfbfbf;
	margin-bottom: 20px;
	padding:5px;
	float: left;
}
.box2{
	float: right;
	width:100px;
	height:100px;
	background:#92a2f1;
	margin-bottom: 20px;
	padding:5px;
}
.box.hover,
.box2.hover{
	background:#81dabe;
}

.hover-off-on-tittle:before{
	content:'Ховер отключен';
	color:red;
}
@media only screen and (min-width:768px) and (max-width:1024px){
	.hover-off-on-tittle:before{
		content:'Ховер включен 768 -- 1024';
		color:green;
	}
}