.scrolling {
	color: white;
	text-align: center;
	width: 100%;

	&.scrolling-down {
		background-color: green;
		transition: all .5s ease;
		transform: scaleY(0);

		[data-scrolldir=down] & {
			transform: scaleY(1);
		}
	}

	&.scrolling-up {
		background-color: red;
		transition: all .5s ease;
		transform: scaleY(0);

		[data-scrolldir=up] & {
			transform: scaleY(1);
		}
	}
}