.star-rating {
	display: flex;
	flex-direction: row-reverse;
	justify-content: flex-end;
	align-items: center;
	font-size: 20px;

	input {
		display: none;
	}

	label {
		padding-left: 5px;
		cursor: pointer;
		color: #fccd64;

		&:last-child {
			padding-left: 0;
		}
	}

	label:before,
	label~label:before,
	input~label:before {
		content: "";
		display: block;
		@include size(18px);
		background-repeat: no-repeat;
		background-image: url('data:image/png;base64,');
	}

	label:hover:before,
	label:hover~label:before,
	input:checked~label:before {
		background-image: url('data:image/png;base64,');
		background-repeat: no-repeat;
	}
}