.my-circle-wr {
	display: flex;
	justify-content: space-around;
}
.my-circle {
	position: relative;
	height: 10rem;
	width: 10rem;
	background: linear-gradient(to bottom, red 10%, orange 20%, green 60%, blue 90%, white 100%);
	border-radius: 50%;

	&:after {
		content: '';
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateX(-50%) translateY(-50%);
		height: calc(100% - 3px);
		width: calc(100% - 3px);
		border-radius: 50%;
		background: #fff;
	}
}

.my-circle-half{
	position: relative;
	height: 10rem;
	width: 10rem;
	background: linear-gradient(to bottom, red 10%, orange 20%, green 60%, blue 90%, white 100%);
	border-radius: 50%;

	&:after {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 50%;
		background: #fff;
	}
}