.page {
	width: 100%;
	background: #000;

	&>h1 {
		font-weight: 300 !important;
		color: #fff;
		font-size: 34px;
		margin: 0;
	}
}

.circles-container {
	min-height: 400px;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-around;
	flex-wrap: wrap;
}

.loader {
	position: relative;
	user-select: none;
	box-sizing: border-box;
	width: 150px;
	height: 150px;
}

.loader-bg {
	width: 100%;
	height: 100%;
	border-radius: 50%;
	box-sizing: border-box;
	border: 5px solid #eeeeee;
	display: flex;
	align-items: center;
	justify-content: center;
	border-color: rgba(255, 255, 255, 0.4);
	box-shadow: 2px 2px 20px 0 rgba(0, 0, 0, 0.6);
}

.spinner-holder-one {
	position: absolute;
	top: 0;
	left: 0;
	overflow: hidden;
	width: 50%;
	height: 50%;
	background: transparent;
	box-sizing: border-box;
}

.spinner-holder-two {
	position: absolute;
	top: 0;
	left: 0;
	overflow: hidden;
	width: 100%;
	height: 100%;
	background: transparent;
	box-sizing: border-box;
}

.loader-spinner {
	width: 200%;
	height: 200%;
	border-radius: 50%;
	border: 5px solid rgb(135, 206, 235);
	box-sizing: border-box;
	border-color: rgb(135, 206, 235);
}

.animate-0-25-a {
	transform: rotate(90deg);
	transform-origin: 100% 100%;
}

.animate-0-25-b {
	transform: rotate(-90deg);
	transform-origin: 100% 100%;
}

.animate-25-50-a {
	transform: rotate(180deg);
	transform-origin: 100% 100%;
}

.animate-25-50-b {
	transform: rotate(-90deg);
	transform-origin: 100% 100%;
}

.animate-50-75-a {
	transform: rotate(270deg);
	transform-origin: 100% 100%;
}

.animate-50-75-b {
	transform: rotate(-90deg);
	transform-origin: 100% 100%;
}

.animate-75-100-a {
	transform: rotate(0deg);
	transform-origin: 100% 100%;
}

.animate-75-100-b {
	transform: rotate(-90deg);
	transform-origin: 100% 100%;
}

.text {
	text-align: center;
	font-size: 20px;
	color: rgb(135, 206, 235);
	font-weight: bold;
	color: rgb(135, 206, 235) !important;
	font-weight: bold;
}

.blue {
	.loader-bg {
		border-color: rgba(255, 255, 255, 0.1);
		box-shadow: inset 2px 2px 20px 0 rgba(0, 0, 0, 0.1) !important;
	}

	.text {
		font-size: 20px;
		color: #3ba8b6 !important;
	}

	.loader-spinner {
		border-color: #3ba8b6;
	}

	&.circle-loaded-75 {
		.text {
			color: #57a3d8 !important;
		}

