.flex-menu {
	list-style-type: none;
	margin: 0;
	background: #eeeeee;
	padding: 0;
	line-height: 1;

	&>li {
		margin: 0 0 0 1.5em;
		float: left;

		&:first-child {
			margin-left: 0;
		}
	}

	a {
		display: block;
		text-decoration: none;
		padding: .25em .25em .25em .25em;
		color: #000;

		&:hover {
			background-color: #006363;
			color: #fff;
		}
	}
}

.js {
	.menu {
		height: 1.5em;
		overflow: hidden;
	}
}

.flex-multi {
	float: left;
	width: 45%;
	margin-bottom: 100px;

	&+.flex-multi {
		float: right;
	}
}

.clear {
	clear: both;
}

.flexMenu-viewMore {
	&>a {
		background-color: #FF9640;
	}
}

.flexMenu-viewMore.active>a,
.flexMenu-viewMore>a:hover {
	background-color: #FF7400;
}

.flexMenu-popup {
	list-style-type: none;
	padding: 0;
	background-color: #5CCCCC;
	margin: 0;

	a {
		display: block;

		&:hover {
			background-color: #006363;
			color: #fff;
			display: block;
		}
	}
}