.multiscroll {
	.ms-section {
		text-align: center;
		font-size: 40px;
		color: #000;
	}
}
.just-link {
	position: fixed;
	bottom:  30px;
	right: 30px;
	z-index: 99;
	color: red;
	font-size: 20px;
	a {
		color: darken(red, 15%);
	}
}
// fix bug list ul with style typography.scss
#multiscroll-nav {
	ul {
		li {
			&:before {
				display: none;
			}
		}
	}
}
.ms-section {
	position: relative;
	box-sizing: border-box;

	&.ms-table {
		display: table;
		width: 100%;
	}
}

.ms-tableCell {
	display: table-cell;
	vertical-align: middle;
	width: 100%;
	height: 100%;
}

.ms-easing {
	transition: all 0.7s ease-out;
}

#multiscroll-nav {
	position: fixed;
	z-index: 100;
	margin-top: -32px;
	top: 50%;
	opacity: 1;

	&.right {
		right: 17px;
	}

	&.left {
		left: 17px;
	}

	ul {
		margin: 0;
		padding: 0;
	}

	li {
		display: block;
		width: 14px;
		height: 13px;
		margin: 7px;
		position: relative;

		a {
			display: block;
			position: relative;
			z-index: 1;
			width: 100%;
			height: 100%;
			cursor: pointer;
			text-decoration: none;
		}

		.active {
			span {
				background: #333;
			}
		}
	}

	span {
		top: 2px;
		left: 2px;
		width: 8px;
		height: 8px;
		border: 1px solid #000;
		background: rgba(0, 0, 0, 0);
		-webkit-border-radius: 50%;
		-moz-border-radius: 50%;
		border-radius: 50%;
		position: absolute;
		z-index: 1;
	}
}

.multiscroll-tooltip {
	position: absolute;
	color: #fff;
	font-size: 14px;
	font-family: arial, helvetica, sans-serif;
	top: -2px;
	white-space: nowrap;
	max-width: 220px;

	&.right {
		right: 20px;
	}

	&.left {
		left: 20px;
	}
}

.ms-responsive {
	.ms-hide {
		display: none;
	}
}

.ms-scrollable {
	overflow: hidden;
	position: relative;
}

.ms-scroller {
	overflow: hidden;
}

.iScrollIndicator {
	border: 0 !important;
}

//#menu
#menu {
	position: fixed;
	top: 0;
	left: 0;
	height: 40px;
	z-index: 70;
	width: 100%;
	padding: 0;
	margin: 0;

	li {
		display: inline-block;
		margin: 10px;
		color: #000;
		background: #fff;
		background: rgba(255, 255, 255, 0.3);
		-webkit-border-radius: 10px;
		border-radius: 10px;

		&.active {
			background: #666;
			background: rgba(255, 255, 255, 1);
			color: #000;

			a {
				color: #000;

				&:hover {
					color: #000;
				}
			}
		}

		a {
			text-decoration: none;
			color: #000;
		}

		&:hover {
			background: rgba(255, 255, 255, 0.8);
		}
	}
}

#menu li a,
#menu li.active a {
	padding: 9px 18px;
	display: block;
}