/* Settings - feel free to play with these */
$overlay_opacity: 0.3; // 0-1
$overlay_pattern: 'http://placehold.it/1920x900';
// Grab the url of a png from here - http://html5backgroundvideos.com/pattern-overlays
.demo-video-wrapper {
	background-image: url("http://placehold.it/1920x900");
}

.video-overlay {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	pointer-events: none;
	background: url($overlay_pattern) left top repeat;
	opacity: $overlay_opacity;
}

.video-hero--content {
	position: relative;
	text-align: center;
	color: #FFF;
	margin: 150px 0;
	text-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
}

.cf:before,
.cf:after {
	content: " ";
	display: table;
}

.cf {
	&:after {
		clear: both;
	}
}


#header-vb {
	background: #2c2d33;
	padding: 2em 0;
}




.jquery-background-video-wrapper {
	position: relative;
	overflow: hidden;
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
}

.jquery-background-video {
	position: absolute;
	min-width: 100%;
	min-height: 100%;
	width: auto;
	height: auto;
	top: 50%;
	left: 50%;
	object-fit: contain;
	transform: translate(-50%, -50%);
}

.js {
	.jquery-background-video {
		opacity: 0;
		transition: opacity 300ms linear;

		&.is-visible {
			opacity: 1;
		}
	}
}

.jquery-background-video-pauseplay {
	position: absolute;
	background: transparent;
	border: none;
	box-shadow: none;
	width: 20px;
	height: 20px;
	top: 15px;
	right: 15px;
	padding: 0;
	cursor: pointer;
	outline: none !important;

	span {
		display: none;
	}

	&.play {
		&:before {
			border-top: 10px solid transparent;
			border-bottom: 10px solid transparent;
			border-left: 15px solid #FFF;
		}
	}

	&.pause {
		&:after {
			left: 10px;
		}
	}
}

.jquery-background-video-pauseplay:after,
.jquery-background-video-pauseplay:before {
	content: "";
	position: absolute;
	left: 0;
	top: 0;
	transition: all .3s ease;
}

.jquery-background-video-pauseplay.pause:before,
.jquery-background-video-pauseplay.pause:after {
	border-top: 10px solid #FFF;
	border-bottom: 10px solid #FFF;
	border-left: 5px solid #FFF;
}