.slick-slider {
	position: relative;
	display: block;
	box-sizing: border-box;
	-webkit-touch-callout: none;
	user-select: none;
	touch-action: pan-y;
	-webkit-tap-highlight-color: transparent;
	.slick-slide {
		&:focus {
			outline: none;
		}
	}
}

.slick-list {
	position: relative;
	overflow: hidden;
	display: block;
	margin: 0;
	padding: 0;
	&:focus {
		outline: none;
	}
	&.dragging {
		cursor: pointer;
		cursor: hand;
	}
}

.slick-slider .slick-track,
.slick-slider .slick-list {
	transform: translate3d(0, 0, 0);
}

.slick-track {
	position: relative;
	left: 0;
	top: 0;
	display: block;
	&:before,
	&:after {
		content: "";
		display: table;
	}
	&:after {
		clear: both;
	}
	.slick-loading & {
		visibility: hidden;
	}
}

.slick-slide {
	float: left;
	height: 100%;
	min-height: 1px;
	padding: 0 5px;
	[dir="rtl"] & {
		float: right;
	}
	img {
		display: block;
		width: 100%;
		height:auto;
	}
	&.slick-loading img {
		display: none;
	}
	display: none;
	&.dragging img {
		pointer-events: none;
	}
	.slick-initialized & {
		display: block;
	}
	.slick-loading & {
		visibility: hidden;
	}
	.slick-vertical & {
		display: block;
		height: auto;
		border: 1px solid transparent;
	}
}

.slick-arrow.slick-hidden {
	display: none;
}

.arrows-box-custom{
	text-align:center;
	padding: 10px;
	button{
		display: inline-block;
		vertical-align:top;
		padding:5px 8px;
		margin: 2px;
		background:#000;
		color:#fff;
	}
}

.slick-dots-custom{
	display: flex;
	padding: 10px;
	margin: 0;
	list-style:none;
	justify-content:center;
	li{
		padding: 2px;
	}
	button{
		display: inline-block;
		background:#000;
		color:#fff;
		padding:5px 8px;
	}
}