// plugin style
.video-container {
	min-height: 500px;
	position: relative;
	overflow: hidden;
	display: flex;
	align-items: center;
	justify-content: center;
}

video {
	object-fit: cover; // Set the magic
	position: absolute;
	height: 100%;
	width: 100%;
	top: 0;
	left: 0;
}

/*** A E S T H E T I C S ***/
/***************************/



.callout {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	height: 100%;
	text-align: center;
	z-index: 10;
}

.support {
	position: absolute;
	bottom: 60px;
	right: 20px;
	text-transform: uppercase;
	font-size: 9px;
	letter-spacing: 1px;
	text-align: right;
	z-index: 10;

	a {
		color: white;
		margin-top: 10px;
	}
}