@keyframes cssload-fold-thecube {

	0%,
	10% {
		transform: perspective(333px) rotateX(-180deg);
		opacity: 0;
	}

	25%,
	75% {
		transform: perspective(333px) rotateX(0deg);
		opacity: 1;
	}

	90%,
	100% {
		transform: perspective(333px) rotateY(180deg);
		opacity: 0;
	}
}

.cssload-thecube {
	width: 178px;
	height: 178px;
	margin: 0 auto;
	margin-top: 119px;
	position: relative;
	transform: rotateZ(45deg);

	.cssload-cube {
		position: relative;
		transform: rotateZ(45deg);
	}

	.cssload-cube {
		float: left;
		width: 50%;
		height: 50%;
		position: relative;
		transform: scale(1.1);
	}

	.cssload-cube:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgb(43, 160, 199);
		animation: cssload-fold-thecube 2.04s infinite linear both;
		transform-origin: 100% 100%;
	}

	.cssload-c2 {
		transform: scale(1.1) rotateZ(90deg);
	}

	.cssload-c3 {
		transform: scale(1.1) rotateZ(180deg);
	}

	.cssload-c4 {
		transform: scale(1.1) rotateZ(270deg);
	}

	.cssload-c2:before {
		animation-delay: 0.255s;
	}

	.cssload-c3:before {
		animation-delay: 0.51s;
	}

	.cssload-c4:before {
		animation-delay: 0.765s;
	}
}

.preloader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgba(0, 0, 0, .9);
}