#load-more {
	overflow: hidden;
}


.column-load-more {
	float: left;
	padding: 20px 50px;
	width: 50%;
}

.column-load-more * {
	margin: 0;
	padding: 0;
}

.post-load-more  {
	background: #ccc;
	border: 1px solid #000;
	padding: 30px;
}

.post-load-more  + .post-load-more  {
	margin-top: 50px;
}

.load-more {
	margin-top: 50px;
}

.post-load-more {
	transition: all 1s linear;
}

.new-item {
	opacity: 0;
	transform: translateY(25px);
}