.bg-stretch-block {
	@include aspect-ratio(1920, 500, ".no-class");

	.bg-stretch {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;

		img {
			object-fit: cover;
			max-width: none;
			height: 100%;
			width: 100%;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}
}