.banner-section {
	position: relative;
	font-size: 30px;
	color: #fff;
	min-height: 300px;

	.container {
		position: relative;
		z-index: 1;
	}

	.bg-stretch {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;

		img {
			object-fit: cover;
			max-width: none;
			height: 100%;
			width: 100%;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}
}