// my custom style
.ui-widget.custom-tooltip-styling, {
	background-color: $color-brand;
	color: #ffffff;
	font-size: 16px;
	font-weight: 400;
	letter-spacing: 0.5px;
	border: none;
	max-width: 170px;
	text-align: center;
	z-index: 6;

	&:before {
		content: "";
		position: absolute;
	}

	&.bottom {
		&:before {
			bottom: 0px;
			left: 50%;
			transform: translate(-50%, 100%);
			@include triangle("down", 12px, 6px, $color-brand);
		}
	}

	&.top {
		&:before {
			top: 0px;
			left: 50%;
			transform: translate(-50%, -100%);
			@include triangle("up", 12px, 6px, $color-brand);
		}
	}
}