.ldBar {
	position: relative;
	width: 300px;
	height: 50px;
	&>.ldBar-label {
		position: absolute;
		top: 0%;
		left: 50%;
		transform: translate(-50%, -50%);
		color: red;
		font-size: 22px;

		&:after {
			content: "%";
			display: inline;
		}
	}
	&.label-center>.ldBar-label {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

	&.no-percent {
		.ldBar-label {
			&:after {
				content: "";
			}
		}
	}
}