.zoom-box {
	float: left;
	width: 100%;
}

.zoom {
	ul {
		&.zoom-list {
			list-style: none;
			margin: 0px 0px 0px 0px;
			padding: 5px 3px 3px 3px;
			float: left;
			border: 1px solid #888A85;
			border-radius: 5px;
		}
	}

	.zoom-list {
		li {
			margin: 0px 0px 0px 0px;
			padding: 0px 0px 0px 0px;
			float: left;
			cursor: pointer;
			font-size: 14px;
			line-height: 14px;
			background: url('../img/sprite-zoom.png') no-repeat top left;

			select {
				border: 1px solid #888A85;
				border-radius: 5px;
				height: 22px;
			}
		}

		.zoom-in {
			background-position: 0 -432px;
			width: 22px;
			height: 22px;
		}

		.zoom-out {
			background-position: 0 -504px;
			width: 22px;
			height: 22px;
		}

		.zoom-left {
			background-position: 0 -216px;
			width: 25px;
			height: 22px;
		}

		.zoom-center {
			background-position: 0 -72px;
			width: 26px;
			height: 22px;
		}

		.zoom-right {
			background-position: 0 -360px;
			width: 24px;
			height: 22px;
		}

		.zoom-bold {
			background-position: 0 0;
			width: 22px;
			height: 22px;
		}

		.zoom-color {
			background-position: 0 -144px;
			width: 31px;
			height: 22px;
		}

		.zoom-reset {
			background-position: 0 -288px;
			width: 22px;
			height: 22px;
		}
	}

	.zoom-color {
		position: relative;

		.color-preview {
			position: absolute;
			top: 4px;
			left: 4px;
			z-index: 1000;
			width: 15px;
			height: 14px;
		}
	}

	.zoom-colors-list {
		list-style: none;
		margin: 0px 0px 0px 0px;
		padding: 3px 0px 0px 3px;
		position: absolute;
		top: 22px;
		left: 0px;
		z-index: 1000;
		width: 73px;
		background-color: #fff;
		border: 1px solid #888A85;
		border-radius: 5px;
		display: none;

		li {
			margin: 0px 3px 3px 0px;
			padding: 0px 0px 0px 0px;
			float: left;
			width: 15px;
			height: 15px;
			border: 0px;
			border-collapse: collapse;
			background-color: #ccc;
			background-image: none;
		}
	}
}

.zoom .zoom-list {

	.zoom-in:hover,
	.zoom-in-active {
		background-position: -33px -432px;
	}

	.zoom-out:hover,
	.zoom-out-active {
		background-position: -33px -504px;
	}

	.zoom-left:hover,
	.zoom-left-active {
		background-position: -33px -216px;
	}

	.zoom-center:hover,
	.zoom-center-active {
		background-position: -33px -72px;
	}

	.zoom-right:hover,
	.zoom-right-active {
		background-position: -33px -360px;
	}

	.zoom-bold:hover,
	.zoom-bold-active {
		background-position: -33px 0;
	}

	.zoom-color:hover,
	.zoom-color-active {
		background-position: -33px -144px;
	}

	.zoom-reset:hover,
	.zoom-reset-active {
		background-position: -33px -288px;
	}
}