//Модульная сетка (flexbox)
//По умолчанию в сборку берётся файл с примесями,
//возвращающими правила модульной сетки.
//Никаких селекторов в CSS не добавляет,
//нужно писать семантические селекторы и вызывать примеси,
//передавая им настройки сетки. Настройки
//по умолчанию вынесены в переменные ($grid-columns: 12; и $grid-gutter-width: 30px;).

// переделал для работы с рабочим объектом $breakpoints с контрольными точками

$screen-sm: map-get($breakpoints, xs-phone);
$screen-md: map-get($breakpoints, phone);
$screen-lg: map-get($breakpoints, tablet);
$screen-xl: map-get($breakpoints, desktop);
$screen-xxl:map-get($breakpoints, macbook);

$grid-columns : 12 !default;
$grid-gutter-width : 30px !default;


//------------------------------------------------------------------------------
//  Генератор правил для прямого родителя модульной сетки
//------------------------------------------------------------------------------
//  Принимает:         По умолчанию:        Пояснение:
//  $all-grid-gutter:  $grid-gutter-width   промежуток между ячейками всегда
//  $xxl-grid-gutter:  $grid-gutter-width   промежуток между ячейками XXL-сетки
//  $xl-grid-gutter:   $grid-gutter-width   промежуток между ячейками XL-сетки
//  $lg-grid-gutter:   $grid-gutter-width   промежуток между ячейками LG-сетки
//  $md-grid-gutter:   $grid-gutter-width   промежуток между ячейками MD-сетки
//  $sm-grid-gutter:   $grid-gutter-width   промежуток между ячейками SM-сетки
//------------------------------------------------------------------------------
// Пример вызова:
// .some-class {
//   @include row (
//     $xs-grid-gutter:  $grid-gutter-width, // отступ по умолчанию
//     $sm-grid-gutter:  0,                  // нет отступа
//     $md-grid-gutter:  $grid-gutter-width  // отступ по умолчанию
//   );
// }
//
// Идентичный вызов:
// .some-class {
//   @include row ( $grid-gutter-width, 0, $grid-gutter-width );
// }
//------------------------------------------------------------------------------
@mixin row (
$all-grid-gutter: $grid-gutter-width,
$xxl-grid-gutter: $grid-gutter-width,
$xl-grid-gutter : $grid-gutter-width,
$lg-grid-gutter : $grid-gutter-width,
$md-grid-gutter : $grid-gutter-width,
$sm-grid-gutter : $grid-gutter-width,
) {
	display: flex;
	flex-wrap: wrap;

	&>* {
		flex-basis: 100%;
	}

	@if ($all-grid-gutter !=0) {
		margin-left: ($all-grid-gutter / -2);
		margin-right: ($all-grid-gutter / -2);
	}

	@if ($xxl-grid-gutter !=$all-grid-gutter) {
		@media (max-width: $screen-xxl) {
			margin-left: ($xxl-grid-gutter / -2);
			margin-right: ($xxl-grid-gutter / -2);
		}
	}

	@if ($xl-grid-gutter !=$xxl-grid-gutter) {
		@media (max-width: $screen-xl) {
			margin-left: ($xl-grid-gutter / -2);
			margin-right: ($xl-grid-gutter / -2);
		}
	}

	@if ($lg-grid-gutter !=$xl-grid-gutter) {
		@media (max-width: $screen-lg) {
			margin-left: ($lg-grid-gutter / -2);
			margin-right: ($lg-grid-gutter / -2);
		}
	}

	@if ($md-grid-gutter !=$lg-grid-gutter) {
		@media (max-width: $screen-md) {
			margin-left: ($md-grid-gutter / -2);
			margin-right: ($md-grid-gutter / -2);
		}
	}

	@if ($sm-grid-gutter !=$md-grid-gutter) {
		@media (max-width: $screen-sm) {
			margin-left: ($sm-grid-gutter / -2);
			margin-right: ($sm-grid-gutter / -2);
		}
	}

}



//------------------------------------------------------------------------------
//  Генератор ширины ячейки
//------------------------------------------------------------------------------
//  Принимает:    По умолчанию:         Пояснение:
//  $column:      1                     ширина (кол-во колонок в этой ячейке)
//  $columns:     $grid-columns;        количество ячеек в ряду
//
//  Возвращает width.
//------------------------------------------------------------------------------
@mixin get-width ($column: 1, $columns: $grid-columns) {
	$width: (100% / $columns) * $column;
	flex: 0 0 $width;
	max-width: $width;
}



//------------------------------------------------------------------------------
//  Генератор правил для одной ячейки
//------------------------------------------------------------------------------
//  Принимает:    По умолчанию:         Пояснение:
//  $column:      1                     ширина (кол-во колонок для этой ячейки)
//  $columns:     $grid-columns;        общее количество ячеек на 1 ряд
//  $gutter:      $grid-gutter-width    промежуток между ячейками
//------------------------------------------------------------------------------
@mixin make-col ( $column : 1,
$columns: $grid-columns,
$gutter : $grid-gutter-width) {
	@include get-width($column, $columns); // ВНИМАНИЕ!
	// если $gutter == false, в CSS не попадут правила padding-left и padding-right
	@if ($gutter !=false) {
		padding-left: ($gutter / 2);
		padding-right: ($gutter / 2);
	}
}



//------------------------------------------------------------------------------
//  Генератор правил для одной ячейки с возможностью указать @media
//------------------------------------------------------------------------------
//  Принимает:    По умолчанию:         Пояснение:
//  $screen:      all                    тип сетки [all|xxl|xl|lg|md|sm]
//  $column:      $grid-columns         ширина (кол-во колонок для этой ячейки)
//  $columns:     $grid-columns;        общее количество ячеек на 1 ряд
//  $gutter:      $grid-gutter-width    промежуток между ячейками
//------------------------------------------------------------------------------
@mixin col ( $screen : all,
$column : $grid-columns,
$columns: $grid-columns,
$gutter : $grid-gutter-width) {

	@if ($screen==all) {
		@include make-col($column, $columns, $gutter);
	}

	@if ($screen==xxl) {
		@media (max-width: $screen-xxl) {
			@include make-col($column, $columns, $gutter);
		}
	}

	@if ($screen==xl) {
		@media (max-width: $screen-xl) {
			@include make-col($column, $columns, $gutter);
		}
	}

	@if ($screen==lg) {
		@media (max-width: $screen-lg) {
			@include make-col($column, $columns, $gutter);
		}
	}

	@if ($screen==md) {
		@media (max-width: $screen-md) {
			@include make-col($column, $columns, $gutter);
		}
	}

	@if ($screen==sm) {
		@media (max-width: $screen-sm) {
			@include make-col($column, $columns, $gutter);
		}
	}
}


//------------------------------------------------------------------------------
//  Генератор правил для отступа одной ячейки с возможностью указать @media
//------------------------------------------------------------------------------
//  Принимает:    По умолчанию:         Пояснение:
//  $screen:      xs                    тип сетки [xs|sm|md|lg|xl|xxl]
//  $column:      1                     ширина отступа (кол-во колонок)
//  $columns:     $grid-columns;        общее количество ячеек на 1 ряд
//------------------------------------------------------------------------------
@mixin col-offset ( $screen: xs,
$column: 1,
$columns: $grid-columns) {

	@if ($screen==xs) {
		margin-left: ($column * 100% / $columns);
	}

	@if ($screen==sm) {

		@media (max-width: $screen-sm) {
			margin-left: ($column * 100% / $columns);
		}
	}

	@if ($screen==md) {

		@media (max-width: $screen-md) {
			margin-left: ($column * 100% / $columns);
		}
	}

	@if ($screen==lg) {

		@media (max-width: $screen-lg) {
			margin-left: ($column * 100% / $columns);
		}
	}

	@if ($screen==xl) {

		@media (max-width: $screen-xl) {
			margin-left: ($column * 100% / $columns);
		}
	}

	@if ($screen==xxl) {

		@media (max-width: $screen-xxl) {
			margin-left: ($column * 100% / $columns);
		}
	}
}

// просто пример как использовать
.row {
	// это прямой родитель сетки, если @include row(); margin по умолчанию (из переменной)
	// иначе задавать в таком порядке [all|xxl|xl|lg|md|sm]
	// должно соответствовать отступам в миксине для колонок @include col(xl, 3, 12, 50px); (последнее значение)
	@include row(70px, 60px, 50px, 40px, 30px, 20px);
}
.row-inner {
	height: 100%;
	padding: 20px;
}
.col-1 {
	// @include col();
	// @include col(all, 12, 12, 70px); // тоже самое что и @include col(); только падинг другой
	@include col(all, 6, 12, 70px);
	@include col(xxl, 2, 12, 60px);
	@include col(xl, 3, 12, 50px);
	@include col(lg, 4, 12, 40px);
	@include col(md, 6, 12, 30px);
	@include col(sm, 12, 12, 20px);
	margin-bottom: 20px;
.row-inner {
	background-color: blue;
}

}

.col-2 {
	// @include col();
	// @include col(all, 12, 12, 70px); // тоже самое что и @include col(); только падинг другой
	@include col(all, 6, 12, 70px);
	@include col(xxl, 10, 12, 60px);
	@include col(xl, 9, 12, 50px);
	@include col(lg, 8, 12, 40px);
	@include col(md, 6, 12, 30px);
	@include col(sm, 12, 12, 20px);
	margin-bottom: 20px;
	.row-inner {
		background-color: tomato;
	}
}