.angle-block {
	background-color: tomato;
	padding: 140px 0px 140px;
	position: relative;
	z-index: 2;
	min-height: 400px;
}

.bac-tr-b {
	position: absolute;
	bottom: -1px;
	width: 100%;
	height: 150px;
	z-index: -1;
	fill: #fff;
}

.bac-tr-t {
	position: absolute;
	top: -1px;
	width: 100%;
	height: 150px;
	z-index: -1;
	fill: #fff;
}